.home-container{
    max-height: 100%;
    width: 100%;
    overflow-y: scroll;
}

.top-components{
    height: fit-content;
    width: 98%;
    display: flex;
    align-items: center;
    justify-content: space-around;
    margin-top: 20px;
}

.box{
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    width: 26%;
    box-shadow: 0 14px 28px rgba(0,0,0,0.25),
    0 10px 10px rgba(0,0,0,0.22);
    padding: 10px 16px 3px;
    border-radius: 20px;
}

.box-right{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    gap: 40px;
    height: 98%;
}

.box-right-top{
    display: flex;
    justify-content: center;
    align-items: center;
}

.box-right-top p{
    position: relative;
    top: 4px;
}

.box-title{
    color: gray;
    font-weight: bolder;
    font-size: 0.9rem;
}

.box-value{
    font-size: 2.1rem;
    font-weight: 500;
}

.box-desc{
    text-decoration: dotted;
    cursor: pointer;
}

.green{
    color: green;
}

.red{
    color: red;
}

.yellow{
    color: rgb(235, 235, 5);
}

.bottom-components{
    width: 98%;
    display: flex;
    margin-top: 30px;
    padding: 20px;
    gap: 25px;
}

.bottom-left{
    flex: 2;
}

.revenue-box{
    box-shadow: 0 10px 20px rgba(0,0,0,0.25),
    0 5px 5px rgba(0,0,0,0.22);
    padding: 5px 10px;
    border-radius: 20px;
}

.revenue-box-bottom{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 10px;
}

.center{
    text-align: center;
}

.revenue-data{
    height: 100px;
}

.values{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: -30px;
}

.values p{
    position: relative;
    top: 4px;
}

.para{
    font-size: 1.1rem;
}

.amount{
    font-size: 2.4rem;
}

.desc{
    font-size: 0.8rem;
    color: gray;
}

.bottom-right{
    flex: 4;
    max-height: 460px;
    object-fit: contain;
    overflow: hidden;
    box-shadow: 0 10px 20px rgba(0,0,0,0.25),
    0 5px 5px rgba(0,0,0,0.22);
    padding: 5px 10px;
    border-radius: 20px;
}